  export default {
      props:{
          noback:{
              type:Boolean,
              default:true
          }
      },
      data() {
          return {
              providerList: []
          }
      },
      methods:{
          async loginEvent(data){
				let res = await this.User.login({
					url:'user/otherlogin',
					data:data,
					Noback:this.noback
				});
				if (res) {
					// 登录成功，重新加载数据
					this.$emit('logining')
				}
			},          
          //登录
          tologin(provider) {
              uni.login({
                  provider: provider.id,



                  success: (res) => {
                      uni.showLoading({
                          title: '登陆中...',
                          mask: false
                      });
                      console.log(JSON.stringify(res))
                      console.log('login success:', res);
                      // 更新保存在 store 中的登录状态
                      console.log("登录成功 保存到本地 修改当前登录状态");
                      
                      uni.getUserInfo({
                          provider:provider.id,
                          success: (infoRes) => {
                              console.log(JSON.stringify(infoRes.userInfo))
                              let data = this.User.__formatOtherLogin(provider.id,Object.assign(infoRes,res));
                              this.loginEvent(data);
                          
                          }
                      });
                      console.log('登录成功，保存到本地存储，修改当前用户登录状态')
                  },
                  fail: (err) => {
                      console.log('login fail:', err);
                  },
                  complete() {
                      uni.hideLoading();
                  }
              });
          },
          //获取登陆渠道
          getLoadingAuth(){
              uni.getProvider({
                  service: 'oauth',
                  success: (result) => {
                      this.providerList = result.provider.map((value) => {
                          let providerName = '';
                          let icon = ''
                          switch (value) {
                              case 'weixin':
                                  providerName = '微信登录'
                                  icon = 'weixin'
                                  break;
                              case 'qq':
                                  providerName = 'QQ登录'
                                  icon = 'QQ'
                                  break;
                              case 'sinaweibo':
                                  providerName = '新浪微博登录'
                                  icon = 'xinlangweibo'
                                  break;
                              case 'xiaomi':
                                  providerName = '小米登录'
                                  icon = 'gengduo'
                                  break;
                              case 'alipay':
                                  providerName = '支付宝登录'
                                  icon = 'gengduo'
                                  break;
                              case 'baidu':
                                  providerName = '百度登录'
                                  icon = 'gengduo'
                                  break;
                              case 'toutiao':
                                  providerName = '头条登录'
                                  icon = 'gengduo'
                                  break;
                              case 'apple':
                                  providerName = '苹果登录'
                                  icon = 'gengduo'
                                  break;
                          }
                          return {
                              name: providerName,
                              icon: icon,
                              id: value
                          }
                      });
                  },
                  fail: (error) => {
                      console.log('获取登录通道失败', error);
                  }
              });
          }
      },
      created() {
          this.getLoadingAuth()
      }
  }
